/* shout out to Anchor Ideas https://codepen.io/AnchorIdeas/pen/eRyeEE */

.titleText {
  font-family: 'Open Sans', sans-serif;
  font-weight: 900;
  font-size: 5em;
  position: absolute;
  top: 25%;
  left: 3%;
  color: #d4d4d4;
  text-shadow: -2px 1px 5px #fafafa;
}

.catColor {
  /*This universally changes the colour of the cat. For multi-coloured cats, delete it and apply background-color to components individually*/
  background-color: #242424;
}

.catContainer {
  position: relative;
  top: -150px;
  left: -680px;
}

@media only screen and (min-width: 960px) {
  .catContainer {
    position: relative;
    top: -350px;
    left: -80px;
  }
}

.catHead {
  width: 170px;
  height: 130px;
  border-radius: 50%;
  -webkit-border-radius: 55% 55% 50% 50%;
  margin-left: 718px;
  margin-top: 200px;
  position: absolute;
  z-index: 2;
  box-shadow: 0px 4px 8px -6px #111;
}

.catHair {
  width: 70px;
  height: 70px;
  position: relative;
  border: none;
  position: absolute;
  margin-top: 208px;
  margin-left: 772px;
}

.catHair::before {
  width: 80px;
  height: 70px;
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  border: none;
  background: inherit;
  -webkit-transform: rotateZ(26deg);
  transform: rotateZ(26deg);
}

.catHair::after {
  width: 72px;
  height: 70px;
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  border: none;
  background: inherit;
  -webkit-transform: rotateZ(60deg);
  transform: rotateZ(60deg);
}

.catEar {
  width: 80px;
  height: 100px;
  border: none;
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  position: absolute;
  z-index: 1;
}

.catLeftEar {
  -webkit-transform: rotateZ(-45deg);
  transform: rotateZ(-45deg);
  margin-left: 717px;
  margin-top: 173px;
}

.catRightEar {
  -webkit-transform: rotateZ(-20deg);
  transform: rotateZ(20deg);
  margin-left: 820px;
  margin-top: 175px;
}

.catInsideEar {
  width: 60px;
  height: 100px;
  border: 1px;
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  background: #cc8888;
  z-index: 1;
}

.catLeftInsideEar {
  margin-left: 12px;
  margin-top: 6px;
}

.catRightInsideEar {
  margin-left: 10px;
  margin-top: 6px;
}

.catEye {
  width: 62px;
  height: 50px;
  -webkit-border-radius: 50%;
  border-radius: 60% 60% 60% 60%;
  background-color: #e6e6e6;
  z-index: 3;
  position: absolute;
  margin-top: 32px;
}

.catLeftEye {
  margin-left: 17px;
}

.catRightEye {
  margin-left: 93px;
}

.eyelid {
  height: 60%;
  width: 100%;
  position: absolute;
  z-index: 10;
  margin-top: -5%;
  border-radius: 100% 100% 10% 10%;
  animation: blink 6s infinite ease-in-out;
  animation-delay: 7s;
}

@keyframes blink {
  1% {
    height: 60%;
  }
  2% {
    height: 80%;
  }
  3% {
    height: 100%;
  }
  4% {
    height: 80%;
  }
  5% {
    height: 60%;
  }
  100% {
    height: 60%;
  }
}

.pupil {
  width: 20px;
  height: 35px;
  background-color: #222;
  -webkit-border-raidus: 50%;
  border-radius: 50%;
  margin-left: 20px;
  z-index: 5;
  margin-top: 3px;
}

.catBody {
  width: 185px;
  height: 110px;
  border: none;
  -webkit-border-radius: 50% / 60% 60% 35% 35%;
  border-radius: 50% / 80% 80% 50% 50%;
  position: absolute;
  margin-left: 712px;
  margin-top: 276px;
  z-index: 1;
}

.catFoot {
  height: 28%;
  width: 30%;
  position: absolute;
  z-index: 10;
  margin-top: 43%;
  border-radius: 100% 100% 70% 70%;
  -webkit-box-shadow: -1px 2px 20px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 2px 20px -5px rgba(0, 0, 0, 0.75);
  box-shadow: -2px -4px 10px -6px rgba(0, 0, 0, 1);
}

.leftFoot {
  margin-left: 8%;
  animation: step 9s infinite ease-in-out;
  animation-delay: 7.4s;
}

.rightFoot {
  margin-left: 60%;
  animation: step 9s infinite ease-in-out;
  animation-delay: 8s;
}

@keyframes step {
  1% {
    transform: translateY(-1px);
  }
  2% {
    transform: translateY(-2px);
  }
  3% {
    transform: translateY(-3px);
  }
  4% {
    transform: translateY(-2px);
  }
  5% {
    transform: translateY(-1px);
  }
  6% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(0px);
  }
}

.catTail {
  position: absolute;
  margin-left: 870px;
  margin-top: 352px;
  translate: rotate(-10deg);
}

/*Each span creates a circle that extends the tail and animates up and down individually, add more and style with left: +5px and a 0.2s additional delay from the previous to create a longer tail.*/

span {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  animation: tailWave 3s infinite ease-in-out;
}

span:nth-child(2) {
  left: 5px;
  animation-delay: 0.2s;
}

span:nth-child(3) {
  left: 10px;
  animation-delay: 0.4s;
}

span:nth-child(4) {
  left: 15px;
  animation-delay: 0.6s;
}

span:nth-child(5) {
  left: 20px;
  animation-delay: 0.8s;
}

span:nth-child(6) {
  left: 25px;
  animation-delay: 1s;
}

span:nth-child(7) {
  left: 30px;
  animation-delay: 1.2s;
}

span:nth-child(8) {
  left: 35px;
  animation-delay: 1.4s;
}

span:nth-child(9) {
  left: 40px;
  animation-delay: 1.6s;
}

span:nth-child(10) {
  left: 45px;
  animation-delay: 1.8s;
}

span:nth-child(11) {
  left: 50px;
  animation-delay: 2s;
}

span:nth-child(12) {
  left: 55px;
  animation-delay: 2.2s;
}

span:nth-child(13) {
  left: 60px;
  animation-delay: 2.4s;
}

span:nth-child(14) {
  left: 65px;
  animation-delay: 2.6s;
}

span:nth-child(15) {
  left: 70px;
  animation-delay: 2.8s;
}

@keyframes tailWave {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}
